
.aboutMe {
    grid-area: a;
    height: 50vh;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
}

.main {
    height: 80%;
    width: 80%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    border: 1px solid;
    background-color: rgb(45%, 45%, 45%);
    color: lightgreen;
}

.greeting {
    height: 50%;
    width: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    /*align-items: center;*/
    /*justify-content: space-between;*/
    font-size: 100%;
    /*margin: 20px;*/
    background-color: darkgray;
    color: white;
}

.img > div > img {
    width:100px;
    height: 100px;
    object-fit: cover;
    grid-template-columns: repeat(auto-fit, minmax(100px, 1fr));
    grid-template-rows: repeat(2, 100px);
    /*border-radius: 50%;*/
    /*margin: 20px;*/
    /*border-color: crimson;*/
}


